.btn-1{
  width: 100px;
  height: 50px;
  border: none;
  color: white;
  background-color: #7BDCA6;
  border-radius: 4px;
  box-shadow: inset 0 0 0 0 #8DA79A;
  transition: ease-out 0.3s;
  font-size: large;
  outline: none;
}

.btn-1:hover{
  box-shadow: inset 100px 0 0 0 #8DA79A	;
  cursor: pointer;
  color: #457371;
}

.btn-2{
  width: 100px;
  height: 50px;
  color: #457371	;
  border-radius: 4px;
  transition: ease-out 0.3s;
  font-size: 15px;
  outline: none;
  border: 3px solid #A4C9C9	;
  position: relative;
  z-index: 1;
}

.btn-2:hover{
  color:  #EED9C4;
  cursor: pointer;
}

.btn-2:before{
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: #2F4F4F	;
}

.btn-2:hover:before{
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
  z-index: -1;
  
}


